import React from 'react';
import {
  withRouter, Route, Redirect, Switch, Link, HashRouter,
} from 'react-router-dom';
import styled from 'styled-components';

const StyledForm = styled.form`
  display: flex;
  align-items: center;
`


class SessionForm extends React.Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleDemo = this.handleDemo.bind(this);
    this.state = {
      username: '',
      password: '',
    };
  }

  handleDemo(e) {
    e.preventDefault();
    const demo = { username: 'joe', password: '123456' };
    this.props.processDemo(demo).then((test) => window.location.reload());
  }

  handleSubmit(e) {
    e.preventDefault();
    const user = { ...this.state };
    this.props.processForm(user).then(() => window.location.reload());
  }

  update(field) {
    return (e) => this.setState({
      [field]: e.currentTarget.value,
    });
  }

  componentWillUnmount() {
    this.props.clearErrors();
  }


  renderErrors() {
    return (
      <ul>
        {this.props.errors.map((error, i) => (
          <li key={`error-${i}`}>
            {error}
          </li>
        ))}
      </ul>
    );
  }

  render() {
    return (
      <div className="login-container">
        <div id="stick">
          <div id="banner">
            <nav id="navbar">
              <div id="logo1">
                <Link to="/">Picture</Link>
              </div>
              <div id="logo2">
                <Link to="/">Perfect</Link>
              </div>
              <div id="other-nav">
                <div id="discover">
                  <Link to="/discover">Discover</Link>
                </div>
                <div id="about">
                  <Link to="/about">About</Link>
                </div>
                <div id="licensing">
                  <Link to="/licensing">Licensing</Link>
                </div>
              </div>
              <div id="auth-buttons">
                <div id="login-button">
                  <Link to="/login" replace>
                    Login
                  </Link>
                </div>
                <div id="signup-button">
                  <Link to="/signup">Signup</Link>
                </div>
              </div>
            </nav>
          </div>
        </div>

        {this.renderErrors()}
        <div id="modalform">
          <StyledForm onSubmit={this.handleSubmit}>
            <div className="login-form">
              {this.props.formType === 'Login' ? (
                <h2 className="modals">
                  Log into PicturePerfect
                </h2>
              ) : <h2 className="modals">Sign up for PicturePerfect</h2>}
              <div id="login-center">
                <div className="anotha-one">
                  <label>Username:</label>
                  <input type="text" value={this.state.username} onChange={this.update('username')} />
                </div>
                <div className="anotha-one">
                  <label>Password:</label>
                  <input type="password" value={this.state.password} onChange={this.update('password')} />
                </div>
                <div id="sub"><input id="submit" type="submit" value={this.props.formType} /></div>
              </div>
              <div onClick={this.handleDemo}>
                <input id="demo-button" type="submit" value="Demo Login" />
              </div>
            </div>
          </StyledForm>
        </div>
      </div>
    );
  }
}
export default withRouter(SessionForm);
